function broadcastSlider() {
    var swiper = new Swiper('.broadcast .swiper-container', {
        slidesPerView: 2,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.broadcast .swiper-button-next',
            prevEl: '.broadcast .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            1200: {
                slidesPerView: 2,
            },
        }
    })
}

function latestSlider() {
    var swiper = new Swiper('.latest-matches .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.latest-matches .swiper-button-next',
            prevEl: '.latest-matches .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            1400: {
                slidesPerView: 2,
            },
            1700: {
                slidesPerView: 3,
            },
        }
    })
}

broadcastSlider()
latestSlider()
